import React from 'react'
import { Box, Typography } from '@material-ui/core'

const Invoice = () => {
    return(
        <>
            <Box width="100%" height="auto" >
                <Box border={1} borderRadius={5} >
                    <Box display="flex" marginY={2} width="100%" justifyContent="center" >
                        <Typography>
                            <Box component="span" fontSize={20} fontWeight="bold" >
                                NOMBRE DEL NEGOCIO
                            </Box>
                        </Typography>
                    </Box>
                    <Box display="flex" px={2} marginY={2} width="100%" >
                        <Typography>
                            <Box component="span" >
                                Fecha : 10/06/2022 - 22:05:52
                            </Box>
                        </Typography>
                    </Box>
                    <Box display="flex" px={2} marginY={2} width="100%" flexDirection="column" >
                        <Typography>
                            <Box component="span" >
                                Cliente : Miguel Moreno
                            </Box>
                        </Typography>
                        <Typography>
                            <Box component="span" >
                                Telefono : 3254788844
                            </Box>
                        </Typography>
                        <Typography>
                            <Box component="span" >
                                Direccion : Manzana 5 casa 24 Conjunto recidencial palmira
                            </Box>
                        </Typography>
                        <Typography>
                            <Box component="span" >
                                Zona : Espinal
                            </Box>
                        </Typography>
                    </Box>
                    <Box px={2} my={2} >
                        <table style={{ width: "100%" }} >
                            <tbody>
                                <tr style={{
                                        borderBottom: "1px solid #b7b7b7",
                                        lineHeight: "25px"
                                }} >
                                    <td> Producto </td>
                                    <td style={{minWidth: "50px"}} > x4 </td>
                                    <td> $ 4.000 </td>
                                </tr>
                                <tr style={{
                                        borderBottom: "1px solid #b7b7b7",
                                        lineHeight: "25px"
                                }}>
                                    <td> Producto con nombre mas largo </td>
                                    <td style={{minWidth: "50px"}} > x44 </td>
                                    <td> $ 4.000.000.000 </td>
                                </tr>
                            </tbody>
                        </table>
                        <Box mt={4} width="100%" fontWeight="bold" display="flex" justifyContent="right" >
                            TOTAL : 4.000.000.000
                        </Box>
                    </Box>
                    <Box display="flex" px={2} marginY={2} width="100%" >
                        <Typography>
                            <Box component="span" >
                                Vendedor : Enrique Pacheco
                            </Box>
                        </Typography>
                    </Box>
                </Box>
            </Box>
        </>
    )
}

export default Invoice