import constant from '../actions/index';

const initialState = {
	list : [],
	limit : 10,
	page : 1,
	unitSizeData : {},
	createUnitSize : {
        activeCreateUnitSizeModal : false,
        form : {
            name : "",
            abbreviation : "",
            baseUnit : {},
            compoundUnit : 0,
            compoundUnitOfMeasure : true
        }
    }
}

export default function UnitSizeReducer( state = initialState, action ){
	let globalState = {...state}
	switch (action.type) {
		case constant.ACTIVATE_MODAL_TO_CREATE_UNIT_OF_MEASURE:
			globalState.createUnitSize.activeCreateUnitSizeModal = action.status
		break;
		case constant.UPDATE_FORM_DATA_CREATE_MEASUREMENT_UNIT:
			globalState.createUnitSize.form[action.attr] = action.value
		break;
		case constant.UPDATE_LIST_OF_UNITS_OF_MEASURE:
			globalState.list = action.unitSizes
		break;
		case constant.UPDATE_DATA_OF_THE_REQUEST_OF_UNITS_OF_MEASUREMENT:
			globalState.unitSizeData = action.data
		break;
		case constant.UPDATE_PAGE_OF_THE_REQUEST_FOR_UNITS_OF_MEASUREMENT:
			globalState.page = action.position
		break;
		default:
			return state
	}
	return globalState
}