import React from 'react'
import { connect } from 'react-redux'
import CreateUnitSize from '.'
import Dialog from '../../../components/Dialog'
import { activeModalToCreateUnitOfMeasure } from '../../../redux/actions/UnitSizeAction'

const mapDispatchToProps = (dispatch) => {
    return {
        activeModalToCreateUnitOfMeasure : (status) => dispatch(activeModalToCreateUnitOfMeasure(status))
    }
}

const mapPropsToState = (status) => {
    return{
        activeCreateUnitSizeModal : status.UnitSize.createUnitSize.activeCreateUnitSizeModal
    }
}

const CreateUnitSizeModal = connect(mapPropsToState, mapDispatchToProps)(({activeCreateUnitSizeModal, ...actions}) => {
    
    const { activeModalToCreateUnitOfMeasure } = actions

    return(
        <>
            <Dialog
                open={activeCreateUnitSizeModal}
                title="Crear unidad de medida"
                close={() => activeModalToCreateUnitOfMeasure(false)}
            >
                <CreateUnitSize />
            </Dialog>
        </>
    )
})

export default CreateUnitSizeModal