async function getProduct(page=1, limit=10,id,name,skucode,provcode,){
    try{
        let url = "/get/products?";
        if( Boolean(page) && Boolean(limit) ) url = url+`page=${page}&limit=${limit}&`;
        if( Boolean(id) ) url = url+`_id=${id}&`
        if( Boolean(name) ) url = url+`name=${name}&`
        if( Boolean(skucode) ) url = url+`skucode=${skucode}&`
        if( Boolean(provcode) ) url = url+`provcode=${provcode}&`
        const data = await app.get(url);
        if(Boolean(data)) return data;
    }catch(err){
        console.log(err)
    }
}
async function saveProduct(product, images){
    try{
        let payload = createFormData(product, images)
        const result = await app.post('/save/product', payload, false)
        return result
    }catch(err){
        console.log(err)
    }
}
async function searchProductFast(text){
    try {
        const elem = document.getElementById('productInput')
        if( !Boolean(elem) ) throw "No existe el elemento"
        if( text.length <= 0 || text.trim() === "") return
        if( elem === elem.ownerDocument.activeElement ){
            const result = await app.get(`/get/products?page=1&limit=10&textFilter=${text}`)
            return result
        }
        return
    } catch (error) {
        console.log(error)
    }
}
async function deleteProduct(products){
    try {
        let identifiers = []
        for( let product of products ){
            if( !Boolean(product._id) ){
                identifiers = products
                console.log(":D")
                break;
            }
            identifiers.push(product._id)
        }
        const result = await app.post('/delete/products', {identifiers})
        return {identifiers, result}
    } catch (error) {
        console.log(error)
    }
}

export default function Product(){
    this.addNewPrice = addNewPrice
    this.removeOnePrice = removeOnePrice
    this.updatePriceList = updatePriceList
    this.validate = validateForm
    this.save = saveProduct
    this.get = getProduct
    this.search = searchProductFast
    this.delete = deleteProduct
}

//Exportables Sync Functions
export function calcProfit(salePrice, cost){
    if(typeof salePrice === "number" && typeof cost === "number"){
        if( salePrice > cost ){
            return Boolean(salePrice-cost) ? salePrice-cost : 0
        }else{
            return 0
        }
    }else{
        console.error("This function only accepts numbers")
    }
}
export function calcProfitPercentaje(cost, salePrice){
    if(typeof cost === "number" && typeof salePrice === "number"){
        if( salePrice >  cost ){
            let total = calcProfit(salePrice, cost)
            let result = (100 / cost ) * total
            return Boolean(result) ? Math.round(parseInt(result)) : 0
        }else{
            return 0
        }
    }else{
        console.error("This function only accepts numbers")
    }
}
export function calcSalePrice(cost, profitPercentaje){
    if(typeof cost === "number" && typeof profitPercentaje === "number"){
        if( profitPercentaje > 0 && cost > profitPercentaje ){
            let result = cost + (profitPercentaje/100*cost)
            return Math.round(parseInt(result))
        }else{
            return 0
        }
    }else{
        console.error("This function only accepts numbers")
    }
}
export function goCreateProduct(businessName){
    history.push(`/${businessName}/create/product`)
}
export function goManageProducts(businessName){
    history.push(`/${businessName}/manage/products`)
}
export function loadProductsFileWindow(){
    const status = store.getState().Products.loadProducts.isOpenModal
    store.dispatch(activeModalLoadMasiveProducts(!status))
}
export function readerFile(files){
    if( Boolean(files[0]) ){
        let readerFile = new FileReader()
        let data = []
        readerFile.onload = event => {
            try{
                const { result } = event.target;
                const workbook = XLSX.read(result, { type: 'binary' });
                for (const sheet in workbook.Sheets) {
                    if (workbook.Sheets.hasOwnProperty(sheet)) {
                        data = data.concat(XLSX.utils.sheet_to_json(workbook.Sheets[sheet]));
                        break;
                    }
                }
                console.log(data)
            }catch(err){
                console.log(err)
            }
        }
        readerFile.readAsBinaryString(files[0]);
    }
}