import constant from ".";
import app from '../../connection/Api'
import parseData from "../../methods/parseData";
import UnitSize from "../../views/unitSize/class";

export const activeModalToCreateUnitOfMeasure = (status) => {
    return { type : constant.ACTIVATE_MODAL_TO_CREATE_UNIT_OF_MEASURE, status }
}
export const updateFormdataCreateMeasurementUnit = (attr, value) => {
    return { type : constant.UPDATE_FORM_DATA_CREATE_MEASUREMENT_UNIT, attr, value }
}
export const updateListOfUnitsOfMeasure = (unitSizes) => {
    return { type : constant.UPDATE_LIST_OF_UNITS_OF_MEASURE, unitSizes }
}
export const updateDataOfTheRequestOfUnitsOfMeasurement = (data) => {
    return { type : constant.UPDATE_DATA_OF_THE_REQUEST_OF_UNITS_OF_MEASUREMENT, data }
}
export const updatePageOfTheRequestForUnitsOfMeasurement = (position) => {
    return { type : constant.UPDATE_PAGE_OF_THE_REQUEST_FOR_UNITS_OF_MEASUREMENT, position }
}

export const submitUnitSize = (payload) => async (dispatch) => {
    try{
        let unitSize = new UnitSize()
        const data = parseData(payload)
        data.baseUnit = data.baseUnit._id
        const resp = await unitSize.create(data)
        console.log(resp)
    }catch(err){
        console.log(err)
    }
}
export const getUnitSize = (limit, page, unitSize_id) => async (dispatch) => {
    try{
        let unitSize = new UnitSize()
        const list = await unitSize.get(limit, page, unitSize_id)
        if( list.status ){
            dispatch(updateListOfUnitsOfMeasure(list.payload))
            dispatch(updateDataOfTheRequestOfUnitsOfMeasurement(list.data))
        }else{
            console.error(list)
        }
    }catch(err){
        console.log(err)
    }
}