import { Grid } from '@material-ui/core'
import React from 'react'
import Autocomplete from '../../../components/Autocomplete'
import Table from '../../../components/Table'
import TitleViews from '../../../components/TitleViews'
import BasicForm from '../../products/create_product/BasicForm'
import PriceForm from '../../products/create_product/PriceForm'
import StockForm from '../../products/create_product/StockForm'

const Form = () => {
    const columns = [
        {
            field: 'name',
            headerName: 'Producto',
            editable: false,
            flex : 1,
        },{
            field: 'name3',
            headerName: 'Cantidad',
            editable: false,
            flex : 1,
        },{
            field: 'name3e',
            headerName: 'Costo',
            editable: false,
            flex : 1,
        },
    ]
    return(
        <>
            <TitleViews mb={2} title="Crear producto compuesto" />
            <form>
                <Grid container spacing={2} >
                    <Grid item xs={6} >
                        <BasicForm getCategories={() => null} getUnitSize={() => null} updateProductForm={() => null} />
                    </Grid>
                    <Grid item xs={6} >
                        <Autocomplete
                            label='Producto base'
                        />
                        <Table
                            // updatePage={(pg) => updateVariantsListPagePosition(pg)}
                            // page={position}
                            // rowCount={variantsTotal}
                            rows={[{name : "Carne molida", name3 : 8, name3e : "$ 40.000", _id : "1asd4as5da6s8d76as413"}]}
                            columns={columns}
                            // overlayText="Cargando unidades de medidas"
                            loading={true}
                        />
                        <PriceForm
                            // priceList={priceList}
                            // calculatePrice={calculatePrice}
                            // updateProductPriceList={updateProductPriceList}
                            // updateSystemMessage={updateSystemMessage}
                            // calculatePricePerPercentage={calculatePricePerPercentage}
                        />
                        <StockForm
                            // useStock={useStock}
                            // updateValueUseStock={updateValueUseStock}
                            // stock={stock}
                            // stockAlert={stockAlert}
                            // updateProductForm={updateProductForm}
                        />
                    </Grid>
                </Grid>
                
            </form>
        </>
    )
}

export default Form