import React from 'react'
import { Box, Chip, Divider, Grid, IconButton } from '@material-ui/core';
import Dialog from '../../components/Dialog';
import Text from '../../components/Text';
import TextField from '../../components/TextField';
import TextFieldMask from '../../components/TextFieldMask';
import { Bankcheck, Money, UnitSize, CashRegisterSVG } from '../../components/icons';
import { ToolsButton } from './ToolBar';
import Market, { focusInSearchbarProduct } from '../market/class'
import Tooltip from '../../components/Tooltips';
import parseData from '../../methods/parseData';

const Results = ({successfulSearch, noResultsFound, productsResults, listPosition, positionPrice, isManualPrice, manualPriceValue, unitOfMeasurePosition, shoppingList, quantity, discount, ...actions}) => {

    const {activateResultList, activateEmptyResultsAlert, updateResults, restoreInitialSettings, updateManualPrice, updateProductQuantity, updateProductDiscount, loadProductConfiguration, restoreInitialPositions, callNotificationSnackbar, activateManualPrice, changeDefaultPrice, changeDefaultUnitOfMeasure, updateSelectedProduct} = actions

    const market = new Market()
    const close = () => {
        if( successfulSearch ) activateResultList(false);
        if( noResultsFound ) activateEmptyResultsAlert(false);
        if( productsResults.length >= 1 ) updateResults([])
    }
    React.useEffect(() => {
        if( successfulSearch && shoppingList.length >= 1 && productsResults.length >= 1 ){
            const productSelected = shoppingList.find(p => p.product._id === productsResults[listPosition]._id)
            if( Boolean(productSelected) ){
                const productConfig = parseData(productSelected.config)
                productConfig.listPosition = listPosition
                loadProductConfiguration(productConfig)
            }else{
                restoreInitialPositions()
            }
        }
    },[shoppingList, productsResults, listPosition])
    React.useEffect(() => {
        if( !successfulSearch ){
            restoreInitialSettings()
            focusInSearchbarProduct()
        }
    },[successfulSearch])
    return(
        <Dialog open={successfulSearch||noResultsFound} close={() => close()} {...(noResultsFound && { severity : 'error', alert :"NO SE ENCONTRARON PRODUCTOS" })} title='Resultados' >
            {productsResults.map((product,index) => {
                const inList = shoppingList.find(p => p.product._id === product._id)
                return(
                    <Box key={index} py={1} onClick={() => updateSelectedProduct(index)} >
                        <Box {...(listPosition===index && {bgcolor:"primary.light"}) } my={1} px={1} borderRadius={5} style={{ cursor : 'pointer' }} position="relative">
                            {Boolean(inList) &&
                                <Tooltip title="Este articulo ya se encuentra en la lista de compras" placement='bottom' >
                                    <Box display="inline-flex" position="absolute" right="0" p={1} borderRadius={50} >
                                        <CashRegisterSVG />
                                    </Box>
                                </Tooltip>
                            }

                            <Text text={`${product.name}`} uppercase fontSize='text' mb={0} lineHeight={1.5} />
                            <Text text={`SKU: ${product.skucode}`} uppercase mb={0} ml={3} color="#ea3a72" lineHeight={1.5} />
                            {listPosition===index &&
                            <>
                                <Box>
                                    <Text
                                        text="DISPONIBLE :"
                                        mb={0}
                                        lineHeight={1}
                                    />
                                    <Text
                                        color="#ea3a72"
                                        text={ product.unitSize.compoundUnitOfMeasure ? ( Boolean(unitOfMeasurePosition) ? `${product.stock*product.unitSize.compoundUnit} (${product.unitSize.baseUnit.abbreviation})` : `${market.calculateBaseStockUnits(product.stock, product.unitSize.compoundUnit)} (${product.unitSize.abbreviation} x${product.unitSize.compoundUnit})` ) : `${product.stock} (${product.unitSize.abbreviation})`}
                                        mb={0}
                                        ml={1}
                                        lineHeight={1}
                                    />
                                </Box>
                                <Box display="flex" >
                                    <ToolsButton
                                        title='Cambiar unidad de medida'
                                        icon={<UnitSize />}
                                        letter="U"
                                        onClick={() => changeDefaultUnitOfMeasure()}
                                    />
                                    <ToolsButton 
                                        title='Precio manual' 
                                        icon={<Bankcheck />} 
                                        letter="M"
                                        onClick={() => activateManualPrice()}
                                    />
                                    <ToolsButton
                                        title='Cambiar precio seleccionado'
                                        icon={<Money />}
                                        letter="P"
                                        onClick={() => changeDefaultPrice()}
                                    />
                                </Box>
                                <Grid container spacing={1} >
                                    <Grid item xs={4} >
                                        <TextField
                                            name="quantity"
                                            size="small"
                                            label="cantidad"
                                            onChange={(evt) => {
                                                const isMayor = evt.value > product.stock
                                                return isMayor ? callNotificationSnackbar(true, "La cantidad seleccionada no puede ser mayor a la cantidad disponible", "warning") : updateProductQuantity(evt.value)
                                                
                                            }}
                                            value={quantity}
                                            InputProps={{
                                                inputComponent : TextFieldMask,
                                            }}
                                            inputProps={{
                                                style : { fontWeight : "600" },
                                                prefix : product.unitSize.compoundUnitOfMeasure ? (Boolean(unitOfMeasurePosition) ? `(${product.unitSize.baseUnit.abbreviation}) - ` : `(${product.unitSize.abbreviation} x${product.unitSize.compoundUnit}) -`) : `(${product.unitSize.abbreviation}) - `,
                                                value : quantity
                                            }}
                                        />
                                    </Grid>
                                    <Grid item xs={4} >
                                        <TextField
                                            name="price"
                                            size="small"
                                            onChange={(evt) => {
                                                const cost = evt.value < product.prices[0].cost
                                                console.log(cost)
                                                return cost ? callNotificationSnackbar(true, `El precio de venta no puede ser menor a ${product.prices[0].cost}`, "warning") : updateManualPrice(evt.value)
                                            }}
                                            disabled={!isManualPrice}
                                            label={ isManualPrice ? "Precio manual" : product.prices[positionPrice].identifier}
                                            value={ isManualPrice ? manualPriceValue : product.prices[positionPrice].salePrice }
                                            InputProps={{
                                                inputComponent : TextFieldMask,
                                            }}
                                            inputProps={{
                                                prefix : "$",
                                                style : { fontWeight : "600" },
                                                value : isManualPrice ? manualPriceValue : product.prices[positionPrice].salePrice
                                            }}
                                        />
                                    </Grid>
                                    <Grid item xs={4} >
                                        <TextField
                                            name="discount"
                                            size="small"
                                            label="Descuento"
                                            onChange={(evt) => updateProductDiscount(evt.value)}
                                            value={discount}
                                            InputProps={{
                                                inputComponent : TextFieldMask,
                                            }}
                                            inputProps={{
                                                prefix : "%",
                                                style : { fontWeight : "600" },
                                                value : discount
                                            }}
                                        />
                                    </Grid>
                                </Grid>
                            </>
                            }
                        </Box>
                        <Divider />
                    </Box>
                )
            })}
        </Dialog>
    )
}

export default Results