import React from 'react'
import { UnitSize } from '../../../components/icons'
import TitleViews from '../../../components/TitleViews'
import Table from '../../../components/Table'
import { goCreateUnitSize } from '../class'
import { connect } from 'react-redux'
import { getUnitSize } from '../../../redux/actions/UnitSizeAction'
import { Box } from '@material-ui/core'
import ActionButtons from '../../../components/ActionButtons'

const mapDispatchToProps = (dispatch) => {
    return {
        getUnitSize : (limit, page) => dispatch(getUnitSize(limit, page)),
    }
}
const mapPropsToState = (state) => {
    return {
        unitSizes : state.UnitSize.list
    }
}

const ManageUnitSize = connect(mapPropsToState, mapDispatchToProps)(({unitSizes, ...actions}) => {
    const { getUnitSize } = actions
    const columns = [
        {
            field: 'name',
            headerName: 'Unidad de medida',
            editable: false,
            flex : 1,
        },{
            field: 'abbreviation',
            headerName: 'Signo/Abreviatura',
            editable: false,
            flex : 1,
            align : "center"
        },{
            field: 'baseUnit',
            headerName: 'Unidad base',
            editable: false,
            flex : 1,
            renderCell : ({row}) => {
                return(
                    <Box display="flex" alignItems="center" >
                        { Boolean(row.baseUnit) ? row.baseUnit.name : "" }
                    </Box>
                )
            }
        },{
            field: 'compoundUnit',
            headerName: 'Cantidad',
            editable: false,
            flex : 1,
            renderCell : ({row}) => {
                return(
                    <Box display="flex" alignItems="center" >
                        {row.compoundUnit >= 1 ? row.compoundUnit : ""} { Boolean(row.baseUnit) ? row.baseUnit.name : "" }
                    </Box>
                )
            }
        },{
            field: 'unitSizeCode',
            headerName: 'Codigo',
            editable: false,
            flex : 1,
            renderCell : ({row}) => {
                return(
                    <Box display="flex" alignItems="center" >
                        {`UNI-${row.unitSizeCode <= 9 ? `0${row.unitSizeCode}` : row.unitSizeCode }`}
                    </Box>
                )
            }
        },{
            field: 'n',
            headerName: ' ',
            align : "center",
            editable: false,
            flex : 1,
            renderCell : (params) => {
                    return <ActionButtons
                    view={false}
                    disabled={false}
                />
            },
        }
    ]

    React.useEffect(() => {
        getUnitSize(10, 1)
    },[])

    return(
        <>
            <TitleViews
                mb={2}
                title="Lista de unidades de medidas"
                tooltip="Crear nueva unidad de medida"
                onClick={() => goCreateUnitSize()}
                icon={<UnitSize />}
            />
            <Table
                // updatePage={(pg) => updateVariantsListPagePosition(pg)}
                // page={position}
                // rowCount={variantsTotal}
                rows={unitSizes}
                columns={columns}
                // overlayText="Cargando unidades de medidas"
                loading={true}
            />
        </>
    )
})

export default ManageUnitSize